@import url('https://fonts.googleapis.com/css2?family=Orbitron:wght@400;600;700;800&family=Rajdhani:wght@400;500;600;700&family=Share+Tech+Mono&display=swap');

:root {
	--font-orbitron: 'Orbitron', sans-serif;
	--font-rajdhani: 'Rajdhani', sans-serif;
	--font-share-tech: 'Share Tech Mono', monospace;

	--transition-default: all 300ms ease-in-out;

	--color-red: #f73939;
	--color-red-darker: #2d0008;
	--color-red-transparent: rgba(247, 80, 73, 0.2);

	--color-cyan: #5ef6ff;
	--color-blue-darker: #002a30;
	--color-orange: #f0b537;
	--color-silver: #cdcdcd;
	--color-black: #181313;
}

/* Hide the scrollbar of the window/element */
*::-webkit-scrollbar {
	width: 0 !important;
}

*::-webkit-scrollbar-track {
	background-color: transparent !important;
}

*::-webkit-scrollbar-thumb {
	background-color: transparent !important;
}

html {
	overflow-x: hidden;
	overflow-y: scroll;
	scroll-behavior: smooth;
}

*,
*::after,
*:not(i)::before,
*::placeholder {
	font-family: var(--font-rajdhani);
	color: var(--color-cyan);
}

body {
	min-height: 100vh;
	line-height: 1.75 !important;
	letter-spacing: 0.02rem !important;

	background-color: #1f040a;
	background-image: url('../images/03.png');
	background-size: cover;
	background-repeat: no-repeat;
	background-position: center center;
}

input:-webkit-autofill,
textarea:-webkit-autofill,
select:-webkit-autofill {
	-webkit-box-shadow: 0 0 0 1000px var(--color-blue-darker) inset !important;
	-webkit-text-fill-color: var(--color-cyan) !important;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	letter-spacing: -0.04375rem !important;
	font-weight: 600 !important;
}

#post-content {
	font-size: calc(1rem + 0.2vw) !important;
}

#post-image {
	width: 100%;
	height: 100%;
	min-height: 200px;
	max-height: 300px;
	background-color: var(--color-cyan);
	background-size: cover;
	background-position: center center;
}

.red-color {
	color: var(--color-red) !important;
}

.background-red {
	background-color: var(--color-red) !important;
}

.cyan-color {
	color: var(--color-cyan) !important;
}

.background-cyan {
	background-color: var(--color-cyan) !important;
}

.orange-color {
	color: var(--color-orange) !important;
}

.background-orange {
	background-color: var(--color-orange) !important;
}

.silver-color {
	color: var(--color-silver) !important;
}

.background-silver {
	background-color: var(--color-silver) !important;
}

.black-color {
	color: var(--color-black) !important;
}

.font-orbitron {
	font-family: var(--font-orbitron) !important;
}

.font-share-tech {
	font-family: var(--font-share-tech) !important;
}

.cyber-glitch-0 {
	animation: cyber-glitch-0 5s linear infinite;
}

.glowing-text-red {
	text-shadow: 0 0 10px var(--color-red) !important;
}

.glowing-text-cyan {
	text-shadow: 0 0 10px var(--color-cyan) !important;
}

.glowing-text-orange {
	text-shadow: 0 0 10px var(--color-orange) !important;
}

.glowing-text-silver {
	text-shadow: 0 0 5px var(--color-silver) !important;
}

.cyber-glitch-1 {
	position: relative;
	top: 10%;
	left: 0%;
	width: 100%;
	margin: 0;
	user-select: none;
}

.cyber-glitch-1::before,
.cyber-glitch-1::after {
	color: inherit !important;
	content: attr(t);
	position: absolute;
	left: 50%;
	transform: translate(-50%, 0.34em);
	width: 100%;
	overflow: hidden;
	animation: cyber-glitch-1 400ms ease-in-out infinite alternate;
}

.cyber-glitch-1::before {
	height: 0.1em;
	line-height: 0.5em;
	opacity: 0.8;
}

.cyber-glitch-1::after {
	content: attr(t);
	top: -10px;
	height: 0.5em;
	line-height: 0.1em;
	opacity: 0.5;
}

.cyber-shake {
	animation: shake 800ms ease-in-out infinite alternate;
}

@keyframes cyber-glitch-0 {
	0% {
		filter: blur(0) brightness(100%) contrast(100%);
	}
	2% {
		filter: blur(0.05rem) brightness(120%) contrast(120%);
	}
	4% {
		filter: blur(0.1rem) brightness(150%) contrast(150%);
	}
	6% {
		filter: blur(0.2rem) brightness(170%) contrast(170%);
	}
	8% {
		filter: blur(0) brightness(100%) contrast(100%);
	}
	10% {
		filter: blur(0) brightness(100%) contrast(100%);
	}
	60% {
		filter: blur(0) brightness(100%) contrast(100%);
	}
	62% {
		filter: blur(0.05rem) brightness(60%) contrast(100%);
	}
	64% {
		filter: blur(0.1rem) brightness(40%) contrast(100%);
	}
	66% {
		filter: blur(0) brightness(100%) contrast(100%);
	}
	68% {
		filter: blur(0.05rem) brightness(60%) contrast(100%);
	}
	70% {
		filter: blur(0.1rem) brightness(40%) contrast(100%);
	}
	72% {
		filter: blur(0) brightness(100%) contrast(100%);
	}
	100% {
		filter: blur(0) brightness(100%) contrast(100%);
	}
}

@keyframes shake {
	0%,
	100% {
		transform: translate(-1px, 0);
	}
	10% {
		transform: translate(2px, 1px);
	}
	30% {
		transform: translate(-3px, 2px);
	}
	35% {
		transform: translate(2px, -3px);
		filter: blur(4px);
	}
	45% {
		transform: translate(2px, 2px) skewY(-8deg) scale(0.96, 1);
		filter: blur(0);
	}
	50% {
		transform: translate(-3px, 1px);
	}
}

@keyframes cyber-glitch-1 {
	0% {
		clip: rect(56px, 9999px, 56px, 0);
	}
	10% {
		clip: rect(78px, 9999px, 6px, 0);
	}
	20% {
		clip: rect(29px, 9999px, 51px, 0);
	}
	30% {
		clip: rect(10px, 9999px, 83px, 0);
	}
	40% {
		clip: rect(18px, 9999px, 31px, 0);
	}
	50% {
		clip: rect(10px, 9999px, 4px, 0);
	}
	60% {
		clip: rect(73px, 9999px, 63px, 0);
	}
	70% {
		clip: rect(80px, 9999px, 41px, 0);
	}
	80% {
		clip: rect(29px, 9999px, 64px, 0);
	}
	90% {
		clip: rect(67px, 9999px, 17px, 0);
	}
	100% {
		clip: rect(32px, 9999px, 70px, 0);
	}
}

.glitch-hover {
	border: 0;
	outline: transparent;
	position: relative;
}

.glitch-hover::after {
	width: 120%;
	--slice-0: inset(30% 50% 50% 50%);
	--slice-1: inset(80% -6px 0 0);
	--slice-2: inset(70% -6px 30% 0);
	--slice-3: inset(10% -6px 85% 0);
	--slice-4: inset(50% -6px 43% 0);
	--slice-5: inset(80% -6px 5% 0);
	content: '';
	display: block;
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background: linear-gradient(var(--color-orange), var(--color-cyan));
	clip-path: var(--slice-0);
}

.glitch-hover:hover::after {
	animation: 250ms glitch;
}

@keyframes glitch {
	0% {
		clip-path: var(--slice-1);
		transform: translate(-20px, -10px);
	}
	10% {
		clip-path: var(--slice-3);
		transform: translate(10px, 10px);
	}
	20% {
		clip-path: var(--slice-1);
		transform: translate(-10px, 10px);
	}
	30% {
		clip-path: var(--slice-3);
		transform: translate(0px, 5px);
	}
	40% {
		clip-path: var(--slice-2);
		transform: translate(-5px, 0px);
	}
	50% {
		clip-path: var(--slice-3);
		transform: translate(5px, 0px);
	}
	60% {
		clip-path: var(--slice-4);
		transform: translate(5px, 10px);
	}
	70% {
		clip-path: var(--slice-2);
		transform: translate(-10px, 10px);
	}
	80% {
		clip-path: var(--slice-5);
		transform: translate(20px, -10px);
	}
	90% {
		clip-path: var(--slice-1);
		transform: translate(-10px, 0px);
	}
	100% {
		clip-path: var(--slice-1);
		transform: translate(0);
	}
}
