
.textArea {
	border-radius: 5px !important;
	border: 2px solid var(--color-cyan) !important;
	box-shadow: 0px 4px 12px 0px rgba(0, 0, 0, 0.2) inset !important;
	background-color: var(--color-blue-darker) !important;
	color: var(--color-cyan) !important;
	font-size: 20px;
}

.label {
	color: var(--color-cyan) !important;
	font-size: 18px;
}

.label::after {
	background-color: transparent !important;
}