.header {
	background-color: rgba(56, 13, 26, 0.4);
	backdrop-filter: blur(25px) !important;
	-webkit-backdrop-filter: blur(10px) !important;
	z-index: 9;
}

.navbarBrand {
	color: var(--color-cyan) !important;
	font-family: var(--font-orbitron) !important;
	font-weight: 600;
}

.navbarCollapse a {
	color: var(--color-red);
	font-weight: 500;
}

.navbarToggler {
	color: var(--color-cyan) !important;
	border: none !important;
	box-shadow: none !important;
}

.navLink {
	transition: var(--transition-default);
	padding-bottom: 0;
	position: relative;
}

.navLink::after {
	content: '';
	position: absolute;
	width: 0;
	height: 1px;
	left: 0;
	top: 100%;
	background-color: var(--color-cyan);
	transition: var(--transition-default);
}

.navLink:hover, .navLink:focus {
	color: var(--color-cyan) !important;
}

.navLink:hover::after, .navLink:focus::after {
	width: 100%;
}
