
.content-card {
	background-color: var(--color-red-transparent);
	transition: var(--transition-default);
	backdrop-filter: blur(4px);
	-webkit-backdrop-filter: blur(4px);
	scale: calc(98%);
	height: 100%;
	width: 100%;
	-webkit-clip-path: polygon(7% 0, 98% 0, 100% 2%, 100% 93%, 93% 100%, 2% 100%, 0% 98%, 0 7%);
	clip-path: polygon(7% 0, 98% 0, 100% 2%, 100% 93%, 93% 100%, 2% 100%, 0% 98%, 0 7%);
}

/* .content-card:hover, .content-card:hover::before, .content-card:hover::after {
	scale: calc(99%);
} */

.card-img-container {
	width: 100%;
	min-height: 200px;
	background-color: var(--color-cyan);
	max-height: 200px;
	height: 100%;
	background-size: cover;
	background-position: center center;
	clip-path: polygon(5% 0, 98% 0, 100% 4%, 100% 93%, 96% 100%, 2% 100%, 0% 97%, 0 9%);
}

.card-title {
	font-weight: 600;
	text-decoration: none;
}

.content-card a:hover {
	text-decoration: underline;
}

.likes:hover,
.dislikes:hover,
.share:hover {
	cursor: pointer;
}

.author_image {
	background-color: var(--color-cyan);
}